import HeaderID from './components/Header/Header'
import CoinList from './components/CoinList/CoinList';
import React, {useState,useEffect} from 'react';
//import './App.css';
import AccountBalance from './components/AccountBalance/AccountBalance';
import styled from 'styled-components';
import axios from 'axios';




const Div = styled.div`
    text-align: center;
    background-color: rgb(2, 2, 70);
    color: #cccccc;
`;

const COST_COUNT = 4;
const formatPrice = price => parseFloat(Number(price).toFixed(4));

function App (props) {
  const [balance, setBalance] = useState(10000);
  const [showBalance, setShowBalance] = useState(true);
  const [coinData, setCoinData] = useState([]);

  /*const [state, setState] = useState({
    balance: 10000,
    showBalance:true,
    coinData: []
  });

  setState(oldState => ({...oldState,balance:12000}));*/
  
  const handleShowBal = () => {
    /*this.setState(function(oldState){
      return{
        ...oldState,
        showBalance: !oldState.showBalance
      }
    });*/
    setShowBalance(oldValue => !oldValue);
  }

  const componentDidMount = async () => {
    const response = await axios.get('https://api.coinpaprika.com/v1/coins');
    const coinIds = response.data.slice(0,COST_COUNT).map(coin => coin.id);
    const tickerUrl = 'https://api.coinpaprika.com/v1/tickers/';
    const promises = coinIds.map(id => axios.get(tickerUrl + id));
    const coinData = await Promise.all(promises);
    const coinPriceData = coinData.map(
      function(response){
        const coin = response.data;
        return{
          key: coin.id,
          name: coin.name,
          ticker: coin.symbol,
          balance: 0,
          price: formatPrice(coin.quotes.USD.price)
        }
      }
    );
    setCoinData(coinPriceData);
    //this.setState({coinData : coinPriceData});
  }

  useEffect( function() {
    if(coinData.length === 0){
      componentDidMount();
    }
  });

  const handleRefresh = async (changevalueId) => {
    /*const tickerUrl = 'https://api.coinpaprika.com/v1/tickers/';
    const newCoinData = this.state.coinData.map(function ( values ) {
    let newValues = { ...values };
    if(changevalueTicker === newValues.ticker){
      let response = await axios.get(tickerUrl + newValues.key);
      let newValues = response.data.slice(0,COST_COUNT).map(
      function(coin){
        return{
          key: coin.id,
          name: coin.name,
          ticker: coin.symbol,
          balance: 0,
          price: parseFloat(Number(coin.quotes.USD.price).toFixed(4))
        }
      });
    }*/
    
    const tickerUrl = `https://api.coinpaprika.com/v1/tickers/${changevalueId}`;
    const response = await axios.get(tickerUrl);
    const newPrice = formatPrice(response.data.quotes.USD.price);
    const newCoinData = coinData.map(function ( values ) {
      let newValues = { ...values };
      if(changevalueId === newValues.key){
        newValues.price =  newPrice;
      }

      return newValues;
      
    });
    

    //console.log(newCoinData);
    /*this.setState(      
      {coinData : newCoinData}
      );*/
    setCoinData(newCoinData);
  }

  
    
  return (
    <Div className="App">
      <HeaderID/>
      <AccountBalance amount = {balance} 
      showBalance={showBalance} 
      handleShowBal={handleShowBal}/>
      <CoinList handleRefresh={handleRefresh} showBalance={showBalance} coinData={coinData}  />
    </Div>
  );
  
  
}

export default App;
